.counter {
  text-align: left;
  }
  .counter h1 {
  font-size: 2.5em;
  color: #000000; /* Heading color */
  }
  .counter p {
  font-size: 1.2em;
  color: #000000; /* Text color */
  }
  .counter h2 {
  font-size: 2em;
  color: #b9004a; /* Counter value color */
  }
  .counter button {
  font-size: 1.2em;
  padding: 10px 20px;
  margin: 0 10px;
  background-color: #ff00dd; /* Button background color */
  color: #fff; /* Button text color */
  border: none;
  border-radius: 5px;
  cursor: pointer;
  }
  .counter button:hover {
    background-color: #b30057; /* Button background color on hover */
    }
    .error {
    color: red; /* Error message color */
    }